
"use client";

import { MainHeader } from "@/components/common/main-header";
import { DashboardCard } from "@/components/dashboard/dashboard-card";
import { PlaceHolderImages } from "@/lib/placeholder-images";
import {
  FileText,
  Scan,
  Mic,
  BarChart3,
  GitBranch,
  FileSignature,
  Camera,
} from "lucide-react";

const features = [
  {
    id: "data-ingestion",
    title: "Process Evidence Files",
    description: "Upload and process various file formats to extract key information.",
    icon: FileText,
    href: "/#",
  },
  {
    id: "scene-reconstruction",
    title: "3D Crime Scene Reconstruction",
    description: "Reconstruct scenes in 3D from images and videos.",
    icon: Scan,
    href: "/3d-reconstruction",
  },
  {
    id: "object-detection",
    title: "Object Detection & Identification",
    description: "Identify weapons, clues, and traces within the crime scene.",
    icon: Camera,
    href: "/object-detection",
  },
  {
    id: "forensic-analysis",
    title: "Forensic Analysis",
    description: "Analyze bloodstain patterns and infer weapon types.",
    icon: BarChart3,
    href: "/forensic-analysis",
  },
  {
    id: "statement-analysis",
    title: "Witness Statement Analysis",
    description: "Transcribe and analyze statements for contradictions or deception.",
    icon: Mic,
    href: "/statement-analysis",
  },
  {
    id: "relational-graph",
    title: "Relational Graph Visualization",
    description: "Connect victims, suspects, and parties to visualize relationships.",
    icon: GitBranch,
    href: "/#",
  },
  {
    id: "report-generation",
    title: "Automated Report Generation",
    description: "Produce exportable reports with summaries and analysis.",
    icon: FileSignature,
    href: "/report-generator",
  },
];

export default function Home() {
  const getImageData = (id: string) => {
    return PlaceHolderImages.find((img) => img.id === id);
  };

  return (
    <div className="flex min-h-screen w-full flex-col">
      <MainHeader title="Veritas AI Dashboard" />
      <main className="flex-1 p-4 md:p-8">
        <div className="mb-8">
          <h1 className="text-3xl font-headline font-bold">Welcome, Investigator</h1>
          <p className="text-muted-foreground">
            Select a tool to begin your analysis.
          </p>
        </div>
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
          {features.map((feature) => {
            const imageData = getImageData(feature.id);
            return (
              <DashboardCard
                key={feature.id}
                title={feature.title}
                description={feature.description}
                icon={feature.icon}
                href={feature.href}
                imageUrl={imageData?.imageUrl}
                imageHint={imageData?.imageHint}
              />
            );
          })}
        </div>
      </main>
    </div>
  );
}
